import React, { Component } from 'react';
import bridge from '@vkontakte/vk-bridge';
//import ScreenSpinner from '@vkontakte/vkui/dist/components/ScreenSpinner/ScreenSpinner';
import '@vkontakte/vkui/dist/vkui.css';

import Home from './panels/Home';
import Oservise from './panels/Oservise';
import Predlog from './panels/Predlog';

import {
	Snackbar, View, ConfigProvider, Avatar, Epic, Tabbar, TabbarItem,
	ModalPageHeader, ModalRoot, IS_PLATFORM_IOS, IS_PLATFORM_ANDROID, ModalPage,
	PanelHeaderButton, Input, Div, FormLayout, Button
} from "@vkontakte/vkui";

import Icon16Clear from '@vkontakte/icons/dist/16/clear';
import Icon16Done from '@vkontakte/icons/dist/16/done';

import Icon24View from '@vkontakte/icons/dist/24/view';
import Icon24Story from '@vkontakte/icons/dist/24/story';
import Icon24Message from '@vkontakte/icons/dist/24/message';
import Icon24Share from '@vkontakte/icons/dist/24/share';
import Icon24Linked from '@vkontakte/icons/dist/24/linked';
import Icon24Cancel from '@vkontakte/icons/dist/24/cancel';
import Icon24Done from '@vkontakte/icons/dist/24/done';

import Icon28InfoOutline from '@vkontakte/icons/dist/28/info_outline';
import Icon28MenuOutline from '@vkontakte/icons/dist/28/menu_outline';
import Icon28AddOutline from '@vkontakte/icons/dist/28/add_outline';



class App extends Component {
	constructor(props) {
		super(props);

		this.state = {
			activePanel: 'games',
			activeStory: 'games',

			tab: 'infected',

			fetchedUser: {
				first_name: 'Test',
				last_name: 'User',
				id: 1
			},
			user: {
				infected: '<загрузка>',
				catched: '<загрузка>',
			},

			coins: '<загрузка>',
			heal: '<загрузка>',

			top: [],
			coinsTop: [],

			popout: null,
			modal: null,

			entered: '',

			copied: false,

			rolled: true,

			scheme: true ? 'bright_light' : 'space_gray'
		};

	}

	componentDidMount() {
		bridge.subscribe(({ detail: { type, data }}) => {
			if (type === 'VKWebAppUpdateConfig') {
				const schemeAttribute = document.createAttribute('scheme');
				schemeAttribute.value = data.scheme ? data.scheme : 'client_light';
				document.body.attributes.setNamedItem(schemeAttribute);
			}
			if (type === 'VKWebAppOpenPayFormResult') {

				if(data.status) {
					this.setState({
						coins: this.state.coins + 1000
					});
					this.openDoneSnackBar('Успешно!');
					bridge.send("VKWebAppSendPayload", {"group_id": 191838894, "payload": {"foo": "bar"}});
				}
			}
			if (type === 'VKWebAppCopyTextResult') {
				this.openDoneSnackBar('Ссылка скопирована!');
				this.setState({
					copied: true
				});
				setTimeout(() => {
					this.setState({
						copied: false
					});
				}, 2000)
				bridge.send("VKWebAppTapticNotificationOccurred", {"type": "success"});
			}
			if (type === 'VKWebAppAllowMessagesFromGroupResult') {
				if(!data.result) return;
				this.openDoneSnackBar('Вы будете получать уведомления о зараженных!');
				bridge.send("VKWebAppTapticNotificationOccurred", {"type": "success"});
			}
			if (type === 'VKWebAppGetUserInfoResult') {
				this.setState({
					fetchedUser: data,
					popout: null
				});
				const catched = parseInt(window.location.hash.replace('#', '')) || '';
				this.getUser(catched);
			}
		});
		bridge.send('VKWebAppGetUserInfo');
		bridge.send("VKWebAppJoinGroup", {"group_id": 191838894});
	}


	getUser = (catched) => {
		this.setState({
			user: {
				infected: '<загрузка>',
				catched: '<загрузка>'
			},
			heal: '<загрузка>',
			coins: '<загрузка>',
		});
		this.api.GetUser(catched).then(res => {
			if(res.logins && res.logins === 0){
				setTimeout(() => {

				}, 2000)
			}
			this.setState({
				coins: res.coins,
				user: res,
				heal: res.heal
			})
		});
	};
	getTop = () => {
		this.setState({
			top: []
		});
		this.api.GetTop().then(res => {
			this.setState({
				top: res
			});
		})
	};
	getCoinsTop = () => {
		this.setState({
			coinsTop: []
		});
		this.api.GetCoinsTop().then(res => {
			this.setState({
				coinsTop: res
			});
		})
	};
	openDoneSnackBar = e => {
		if(this.state.snackbar){
			this.setState({
				snackbar: null
			});
			return;
		}
		this.setState({ snackbar:
				<Snackbar
					layout="vertical"
					duration={3000}
					onClose={() => this.setState({
						snackbar: null
					})}
					before={
						<Avatar
							size={24}
							style={{ backgroundColor: '#4bb34b' }}
						><Icon16Done fill="#fff" width={14} height={14} />
						</Avatar>
					}
				>
					{e}
				</Snackbar>
		});
	};

	render() {
		const go = (e,a) => {
			this.setState({
				activePanel: e,
				activeStory: a ? a : this.state.activeStory,
				snackbar: null
			});
		};

		const { openDoneSnackBar, getTop, getUser, getCoinsTop } = this;

		const openErrorSnackBar = e => {
			if(this.state.snackbar){
				this.setState({
					snackbar: null
				});
				return;
			}
			this.setState({ snackbar:
					<Snackbar
						duration={3000}
						layout="vertical"
						onClose={() => this.setState({
							snackbar: null
						})}
						before={
							<Avatar
								size={24}
								style={{ backgroundColor: '#FF0000' }}
							><Icon16Clear fill="#fff" width={14} height={14} />
							</Avatar>
						}
					>
						{e}
					</Snackbar>
			});
		};

		const makeStory = () => {
			let url = `https://vk.com/covid19_app#${this.state.fetchedUser.id + 524069408}`;
			bridge.send("VKWebAppShowStoryBox", {
				"background_type" : "none",
				"attachment": {
					"text": "go_to",
					"type": "url",
					"url": url
				} });
		};

		const closeModal = () => {
			this.setState({
				modal: null
			})
		};

		const buyHeal = () => {
			if((this.state.coins >= 30)){
				this.api.BuyHeal().then(res => {
					if(res === 'success'){
						this.setState({
							coins: this.state.coins - 30,
							heal: this.state.heal + 1
						});
						openDoneSnackBar('Успешно')
					}
				});
			} else {
				openErrorSnackBar('Баланса недостаточно')
			}
		};

		const openModal = () => {
			this.setState({
				modal: '1'
			})
		};

		const modalka = (
			<ModalRoot
				activeModal={this.state.modal}
				onClose={closeModal}
			>
				<ModalPage
					id='1'
					onClose={closeModal}
					header={
						<ModalPageHeader
							left={IS_PLATFORM_ANDROID && <PanelHeaderButton onClick={closeModal}><Icon24Cancel /></PanelHeaderButton>}
							right={<PanelHeaderButton onClick={closeModal}>{IS_PLATFORM_IOS ? 'Готово' : <Icon24Done />}</PanelHeaderButton>}
						>
							Заразить
						</ModalPageHeader>
					}
				>
					<Div>
						<FormLayout>
							<Input
								value={`vk.com/covid19_app#${this.state.fetchedUser.id + 524069408}`}
								readOnly
							/>
							<Button
								disabled={this.state.copied}
								size='xl'
								onClick={() => {
									bridge.send("VKWebAppCopyText", {text: `vk.com/covid19_app#${this.state.fetchedUser.id + 524069408}`});
								}}
								before={<Icon24Linked/>}>{this.state.copied ? 'Скопировано' : 'Скопировать'}</Button>
							<Button
								size='xl'
								onClick={() => {
									bridge.send("VKWebAppShowWallPostBox", {"message": `vk.com/covid19_app#${this.state.fetchedUser.id + 524069408}`});
								}}
								before={<Icon24Share/>}>На стену</Button>
							<Button
								size='xl'
								onClick={() => makeStory()}
								before={<Icon24Story/>}>В историю</Button>
							<Button
								size='xl'
								onClick={() => {
									bridge.send("VKWebAppShare", {"link": `https://vk.com/covid19_app#${this.state.fetchedUser.id + 524069408}`});
								}}
								before={<Icon24Message/>}>Отправить сообщением</Button>
							<Button
								style={{
									color: '#fff',
									backgroundColor: '#4bb34b'
								}}
								size='xl'
								onClick={() => {
									bridge.send("VKWebAppAllowMessagesFromGroup",  { "group_id": 186316619 });
								}}
								before={<Icon24View/>}>Уведомлять о заражении</Button>
						</FormLayout>
					</Div>
				</ModalPage>
			</ModalRoot>
		);
		const {
			fetchedUser, activePanel, activeStory, popout, snackbar
		} = this.state;

		const state = this.state;

		const props = {
			openDoneSnackBar, openErrorSnackBar, fetchedUser, go, snackbar, state,
			setParentState: this.setState.bind(this),
			openModal, getTop, getUser, buyHeal, getCoinsTop
		};
		const modal = modalka;
		const view = { popout, modal };

		return (
			<ConfigProvider isWebView scheme={state.scheme}>
			<Epic activeStory={activeStory} tabbar={
				<Tabbar>
					<TabbarItem
						onClick={() => go('games', 'games')}
						selected={this.state.activeStory === 'games'}
					><Icon28MenuOutline /></TabbarItem>
					<TabbarItem
						onClick={() => go('predlog', 'predlog') }
						selected={this.state.activeStory === 'predlog'}
					><Icon28AddOutline /></TabbarItem>
						<TabbarItem
						onClick={() => go('oservise', 'oservise') }
						selected={this.state.activeStory === 'oservise'}
					><Icon28InfoOutline /></TabbarItem>
				</Tabbar>
			}>
				<View
					id='games'
					activePanel={activePanel}
					onSwipeBack={() => go('games')}
					history={ activePanel !== 'games' ? ['games', activePanel] : ['games']}
					{...view}
				>
					<Home style={{ userSelect: 'nona' }} id='games' {...props} />
					<Predlog id='predlog' {...props} />
					<Oservise id='oservise' {...props} />

				</View>
				<View id='predlog' activePanel='predlog' {...view}>
					<Predlog id='predlog' {...props} />
				</View>

				<View id='oservise' activePanel='oservise' {...view}>
					<Oservise id='oservise' {...props} />
				</View>

			</Epic>
			</ConfigProvider>
		);
	}

}

export default App;

